.features_container{
    width: 100%;
    margin-top: 15rem;
    margin-bottom: 8rem;
}
.list_features_container{
    width: 100%;
    padding: 0 2rem;
    @include flex(space-evenly,start,wrap,row);

}
.list_features_explaination .common_featues_container{
    margin-bottom: 2rem;
    h1{
        font-size: 2rem;
        max-width: 200px;
        margin-right: 8rem;
    }
}
.list_features_heading{
    // margin: 0 10rem;

    max-width: 34.2rem;
    margin-bottom: 4rem;
    h1{
        font-size: 3rem;
        @extend %common_gradient;
    }
    h3{
        margin:1rem 0;
        background: linear-gradient(
            89.97deg
            , #db5336 1.84%, #FF8A71 102.67%);
            background-size: 100%;
            -webkit-background-clip: text;
            -moz-background-clip: text;
            -webkit-text-fill-color: transparent;
            -moz-text-fill-color: transparent;
    font-size: 1.5rem;
    }
}

// .........................................
        //   second part started 
        .features_img_container{
padding: 1rem;
            @include flexcc_wrap;
            justify-content: space-evenly;
            align-items: flex-end !important;
        }
        .feature_img{
            width: 45rem;
            img{ width: 100%;}
        }
        .feature_img_text_container{
            width: 40rem;
            display: flex;
            justify-content: start;
            align-items: flex-start;
            flex-direction: column;
            flex-wrap: nowrap;
            h3,h4{
                font-size: 1.6rem;
                margin: 1rem 0;
                color: #71E5FF;

            }
            h4{color:#FF8A71; margin-top: 3rem;}
            h1{
                font-size: 3.4rem;
                @extend %common_gradient;
            }
            p{
                font-size: 1.6rem;
                margin: 1.5rem 0;
                color: #81AFDD;
            }
        }
// ....................................... 




        .feature_add{
            width: 78%;
            max-width: 129rem;
            margin: 0 auto;
            border-radius: 1rem;
            background: linear-gradient(89.97deg, #AE67FA 1.84%, #F49867 102.67%);
           display: flex;
           justify-content: start;
           align-items: center;
           padding: 3rem 1rem;
           div{
            display: flex;
            justify-content: center;
            align-items: flex-start;
            flex-direction: column;
            h3{
                font-size: 1rem;
                color: #0E0E0E;
                text-align: center;
            }
            h1{
                text-align: center;
                font-size: 2rem ;
            }
        }
         button{
            outline: none;
            border: none;
            padding: 1rem 2rem;
            border-radius: 30px;
           
           background: #000000;
            color: white;
            margin-left: auto;
         }



    }


@media only screen and (max-width:850px) 
{
    .list_features_explaination .common_featues_container{
        h1{
            margin-right: 0px !important;
        }
}
.feature_add{
    width: 95%;
}
}
@media only screen and (max-width:700px) 
{
    .feature_add{
flex-direction: column;
width: 95%;
    div{
         align-items: center;
         margin-bottom: 2rem;
    }
      button{
          margin:0rem 0rem !important;
      }
    }
}