.footer_container
{
    background: #031B34;
    padding-top: 18rem;
@include flex(center,center,nowrap,column);

h1{
    @extend %common_gradient;
    font-size: 5rem;
    max-width: 56rem;
   
    text-align: center;

}
button:hover{
    background: #6b9ab554;
}
button:hover,.footer_div a:hover{
    transform: scale(1.1);
    transition: 0.6s;
}
button{
    margin: 3rem 0;
    border: 1px solid white;
    font-size: 1.6rem;
    background: transparent;
    transition: 0.6s;

    padding:  1.5rem 1.8rem;
    color: rgb(255, 255, 255);
}
footer{
    width: 100%;
    margin-top: 4rem;
    @include flex(space-evenly,start,wrap,row);
padding: 0 0.5rem;
padding-top: 5rem;
}

.footer_div{
    @include flex(start,start,nowrap,column);
margin: 3rem ;

  img{
      width: 20rem;
  }
  p{
      color: white;
      font-size: 1.2rem;
      max-width: 19rem;
      text-align: left;
      margin: 3rem 0;
  }


    h3{
        font-size: 2rem;
        margin-bottom: 1.5rem;
        
        color: white;
    }
    h6,a{
        margin: 2rem 0;
        color: white;
    transition: 0.6s;
    font-weight: 100;
        font: menu;

        font-size: 1.4rem;
    }


}




}

.copy_right{
    width: 100%;
    @include flexcc;
    padding-top: 3rem;
    padding-bottom: 4rem;
    font-size: 1.2rem;
    color: white;
}

@media only screen and (max-width:500px)
{
.footer_container footer .footer_div{
        @include flex(start,center,nowrap,column);
}
}