.what_gtp3_container{
    max-width: 1050px;
    background: linear-gradient(148deg, #011f46 74%, #0e83d7 138%);
    box-shadow: 1px 1px 8px black, inset 1px 1px 68px #0e2f5e;
    margin: 0 auto;
}

.gpt3_top_heading{
    padding-top: 4rem !important;
    width: 100%;
    padding:  0 2rem;

    .common_featues_container{
        p {
            max-width: 50rem !important;
        }
    }

}
.common_featues_container{
    width: 100%;
    @include flex(space-between,start,nowrap,row);
  h1{position: relative;
    font-size: 2.5rem;
    color: white;
    margin: 2rem 0 ;
   }
   p{
       max-width: 22rem;
       font-size: 1.6rem;
       color: #81AFDD;

   }
}
.common_featues_container h1::after{
    content: "";
    position: absolute;
    height: 3px;
    top: -9%;
    left: 0%;
    z-index: 5;
    width: 30%;
    @include gradient();
}
.gpt3_mid_heading_container{
    width: 100%;
    padding: 1rem;
    margin:2rem 0 ;
    @include flex(space-between,center,nowrap,row);
    h1{
        font-size: 4rem;
        max-width: 40rem;
        letter-spacing: -0.04em;
        margin: 0 1rem;
          @extend %common_gradient;
        }
        h3{
            background: linear-gradient(
                89.97deg
                , #db5336 1.84%, #FF8A71 102.67%);
                background-size: 100%;
                -webkit-background-clip: text;
                -moz-background-clip: text;
                -webkit-text-fill-color: transparent;
                -moz-text-fill-color: transparent;
        font-size: 1.5rem;
        // color: white;
    }
}

.last_div_container{
    // display: grid;
    // grid-template-columns: 1fr 1fr 1fr;
    // grid-template-rows: 1fr;
    // grid-column-gap: 1rem;
    // padding: 2rem 2rem;


    display: flex;
    padding: 2rem 2rem;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 5rem;
}
.last_div_container .common_featues_container
{
    flex-direction: column !important;
    justify-content: center !important;
    max-width: 300px;
    flex-wrap: wrap;
    p{text-align: start !important;}
    h1{font-size: 2.5rem !important;}
}


@media only  screen and (max-width:1100px) 
{
    .what_gtp3_container{
        width:90% !important;
    }
}
@media only  screen and (max-width:450px) 
{
    .what_gtp3_container{
        width:96% !important;
    }
}
@media only  screen and (max-width:830px) 
{
    .common_featues_container{
        width: 100%;
        @include flex(center,center,nowrap,column);
        p{text-align: center;}
    }
.gpt3_mid_heading_container{
    @include flex(center,center,nowrap,column);

h1,h3
    {text-align: center !important;}

}
.last_div_container .common_featues_container p{
    text-align: center !important;
}
}