.blog_container{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin: 5rem auto;
    margin-top: 10rem;
    max-width: 90rem;
    h1{
        max-width: 40rem;
        margin-bottom: 4rem;
    text-align: left;
    font-size: 4.2rem;
        @extend %common_gradient;
    }
    padding: 1rem;
}

.blog_post_container
{
    // @include flex(center,center,wrap,row);
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-rows: 1fr;
    column-gap: 1rem;
    width: 100%;
    min-height: 61rem;

}
.blog_img_container {
    img{

        max-width: 100%;
    }
     width: 100%;
}
.blog_left_post,.blog_right_post{
    height: 100%;
}
.blog_right_post{
    .post_parent 
    {
      h2{font-size: 1.9rem;}
    }
}
.blog_left_post{
    .post_parent 
    {
      h2{font-size: 2.5rem;}
      max-width: 30rem;
      img{
          width: 100%;
      }
      height: 100% !important;
    }
}

.post_parent:hover{
    transform: scale(1.1);
}
.post_parent{
    height: 30rem;
    width: 28rem;
    transition: 0.5s;
    cursor: pointer;
    background: #042C54;
     @include flex(start,flex-start,nowrap,column);
     h2{
         max-width: 100%;
         color: white;
         
         
         font-size: 2rem;
         margin: 1rem;
        }
        h3{
            color: rgb(209, 209, 209);

         font-size: 1.1rem;
         margin: 1rem;
        }
        h6{
            margin: 1rem;
            color: rgb(209, 209, 209);
            margin-top: auto;
            font-size: 1.1rem;
     }

}



.blog_right_post{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;

    grid-row-gap: 2rem;
    grid-column-gap: 3rem;

}


@media only screen and (max-width:950px) 
{
    .blog_post_container{
        //    display: flex;
        //    @include flex(center,center,wrap,row);
        grid-template-columns: 1fr;
    }
    
    
    .blog_right_post{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        .post_parent 
        {
            margin: 0 1rem ;
        }
    }
    .blog_left_post{
        margin: 2rem 0;
        
        .post_parent 
        {
            margin: auto;
            height: 40rem !important;
     
            
            
        }
    }
    .blog_container #blog_container_heading {
        font-size: 3.2rem;
    }
    
}
@media only screen and (max-width:550px) {
    .blog_left_post{
        .post_parent 
        {
            width: 95% !important;
}
}

}
@media only screen and (max-width:310px) {
    .post_parent{
        width: 95%;
    }
.blog_container #blog_container_heading {
    font-size: 2.2rem  !important;
}
}