body
{
    header{

        width: 100%;
    }
 // min-height: 100vh;
    /* ff 3.6+ */
   
}

$margin:3rem 0rem;


.header_svg_container img
{
    max-width: 47rem;
    @include flexcc_col;
}
//_____________________________________________
.header_row{
    @include flex(center,center,wrap,row);
    width: 100%;

}
//_____________________________________________
.header_text_container{
    max-width: 50rem;
    padding: 0 2rem;

}
//_____________________________________________
.header_text{
    max-width: 400px;
    font-size: 2rem;
    margin: $margin;

}
//_____________________________________________
.header_button_container{
    width: 100%;
    @include flex(start,center,nowrap,row);
    margin: $margin;
    
}
.header_button_container button
{
        font-size: 2rem;
        outline: none;
        color: white;
        padding: 1rem;
        background: #FF4820;
        border-radius: 0px 0.3em 0.3em 0px;
border: none;

        
    }
    .header_button_container input::placeholder{
        color: #81AFDD;
    }
    .header_button_container input
    {
        padding: 1rem;
    outline: none;
    border: none;
    font-size: 2rem;
    width: 32rem;
    background: #052D56;
border-radius: 5px 0px 0px 5px;

}
//_____________________________________________
.header_contact_container p{
    font-size: 1.2rem;
    color: white;
     margin:0 0.5rem;
}
.header_contact_container{
    @include flex(start,center,wrap,row);
  width: 100%;
  margin: $margin;

}
//_____________________________________________
//_____________________________________________
.brand_container{
    width: 100%;
    margin-top: 5rem;
    @include flex(center,center,wrap,row);

    div{
        margin: 3rem;
        img:hover{
            transition: 0.8s;
            transform: scale(1.1);
        }
    }
}
//_____________________________________________
.header_heading{
    font-family: Manrope;
font-style: normal;
font-weight: 800;
font-size: 5.2rem;
// line-height: 75px;
/* or 121% */

margin: $margin;
letter-spacing: -0.04em;


@extend %common_gradient;
}







@media only screen and (max-width:600px)
{
    .header_svg_container
    {
        width: 100%;
        img{
            width: 100%;

        }
    }
    .header_button_container input
{
    width: 100%;
}
    
}
@media only screen and (max-width:400px) {
    .header_heading{
      font-size: 3.2rem;
    }
}
@media only screen and (max-width:600px) 
{
    .header_heading{
        font-size: 4.2rem;
    }
    .header_button_container
    {
        flex-direction: column !important;
        button{
            margin: 2rem 0;
            border-radius: 0.3em !important;
        }
    }
}
@media only screen and (max-width:270px) {
    .header_heading{
      font-size: 2.2rem;
    }
}