nav{
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items:center;
    position: relative;
    padding: 2rem 7rem;
}
.nav_buttons
{
    @include flex(center,center,nowrap,row);
    button{
        background: none;
        border: none;
        color: white;
        font-family: Manrope;
        font-style: normal;
        font-weight: 500;
        margin: 0.5rem;
        font-size: 1.5rem;
        padding: 0.7rem 1rem;
        outline: none;
        cursor: pointer;
        border-radius: 0.5rem;
        color: #FFFFFF;
    }
    #sign_up{

        background: #ff4920;
      
    }
    #sign_in:hover{
background: rgba(0, 0, 0, 0.582);
    }
}

#bars{
    display: none;
}

#logo {margin-right: 1rem;}
#logo img{
width: 5rem;
}

.nav_links li:hover,.nav_buttons button:hover{
    transition: 0.5s;
    cursor: pointer;
    transform: scale(1.1);
}
.nav_links{
    @include flexcc;
transition: 0.5s;
margin-right: auto;
    li{
        margin: 1rem;
    }

 li a{
     color: white;
     font-size: 1.5rem;

 }   
}




@media only screen and (max-width:700px) 
{
#logo {margin-right: 0rem;}

    .nav_links{
        position: absolute;
        flex-direction: column;
        top: 0;
        width: 100%;
        background-color: rgba(0, 0, 0, 0.863);
        right: 100%;
        height: 100vh;
        margin-right: 0px;
       a{
           font-size: 2rem !important;
           text-shadow: 1px 2px 12px white;
       }
    }
nav
{
    padding: 2rem 0px;
}
    #bars{
        display: inline;
        color: white;
        font-size: 2rem;
        z-index: 22;
        margin-left: 1rem;
    }
    
}