.post_parent{
    box-shadow: 1px 1px 12px black !important;
}

%common_gradient{
   
   background-color: #f3ec78;
    // background-image: linear-gradient(45deg, #f3ec78, #af4261);
background: linear-gradient(89.97deg, #AE67FA 1.84%, #F49867 102.67%);

    background-size: 100%;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent; 
    -moz-text-fill-color: transparent;
        
    }
@mixin  gradient {
    background-color: #f3ec78 !important;
    background: linear-gradient(
89.97deg
, #AE67FA 1.84%, #F49867 102.67%);
    background-size: 100%;

    
}
body{
    background: linear-gradient(
        131deg
        , #062342 11%, #010b1a 27%, #020d1e 100%) !important;}

    @import "./common";
    @import "../components/header_nav/navbar/navbar";
    @import "../components/header_nav/header/header";
    @import "../components/what_gpt-3/gpt_3";
    @import "../components/features/features";
    @import "../components/blog/blog";
    @import "../components/footer/footer";

    button:hover{
        transform: scale(1.1);
        transition: 0.6s;
    }


 .post_parent {
        /* overflow: hidden; */
        border-radius: 35px 0px 0px 0px !important;
    }

    .common_animation{
        
    animation: common_animations;

    transition: 0.5s;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
    animation-direction: alternate;
    }
    @keyframes common_animations 
    {
        0%{
             transform: scale(1.0);
            }
            100%{
            transform: scale(1.05);

        }
        
    }


 ::-webkit-scrollbar{
     width: 10px;
 }
 ::-webkit-scrollbar-thumb{
     border-radius: 10px;
     
     background-color: white;
    }
    ::-webkit-scrollbar-track{
        background-color: rgb(46, 46, 46);
 }